import {
	Table,
	TableCaption,
	TableContainer,
	Thead,
	Tr,
	Th,
	Td,
} from '@chakra-ui/react'
import UseThemeColors from '../Hooks/useThemeColors'
import { useThoughtRecordApi } from '../Hooks/useThoughtRecordApi'

const Profile = () => {
	const { textColor } = UseThemeColors()
	const getFormattedDate = (time: number) => {
		const date = new Date(time)
		return date.toLocaleDateString()
	}
	const { getAllThoughtRecords } = useThoughtRecordApi()
	const thoughtRecords = getAllThoughtRecords()
	if (!thoughtRecords) {
		return <></>
	}
	return (
		<>
			<TableContainer>
				<Table variant="simple">
					<TableCaption> Your Thought Records </TableCaption>
					<Thead>
						<Tr>
							<Th>Recorded:</Th>
							<Th> Felt Before </Th>
							<Th> Felt After </Th>
						</Tr>
					</Thead>
				</Table>
			</TableContainer>
		</>
	)
}

export default Profile
